/** @jsx jsx */
import * as React from "react";
import { Themed } from "@theme-ui/mdx";
import { css, jsx } from "theme-ui";
import { StaticImage } from "gatsby-plugin-image";

const NotFoundPage = () => {
  return (
    <Themed.root
      css={css({
        display: `flex`,
        flexDirection: `column`,
        alignItems: `center`,
        justifyContent: `center`,
        minHeight: `100vh`,
      })}
    >
      <Themed.div css={css({ width: `100%` })}>
        <StaticImage
          src="../images/splash_icon2.png"
          alt="Logo"
          style={{
            maxHeight: 200,
            maxWidth: 200,
            margin: `auto`,
            quality: 100,
          }}
          layout="fullWidth"
        />
        <Themed.p
          sx={{
            fontSize: 6,
            textAlign: `center`,
            color: `black`,
            mb: 3,
            mt: 3
          }}
        >
          Nic tu nie ma ðŸ¤¨
        </Themed.p>
      </Themed.div>
    </Themed.root>
  );
};

export default NotFoundPage;

export const Head = () => <title>Not found</title>;
